




























































import goodsCard from 'template/goods_card'
import naData from 'template/no_data'
import login from 'auth/login'
export default {
	components: {
		goodsCard,
		naData,
		login
	},
	data() {
		return {
			isShowNum: true,
			selectAll: false,
			list: [],
			ismask: false,
			overdueList: [],
			listInfo: {},
			currentTab: 0,
			isEdit: false,
			cargoNum: 0,
			tabs: ['热卖专区', '购物商城', '积分商城'],
			// zoneInfo: {},
			// showPop: false,
			isVip: 0,
			nodata: false,
			nodataText: "购物车为空",
			nodataImg: '/static/img/empty_cart.png',
		}
	},
	onLoad() {

		// this.getZone()
	},
	onShow() {
		this.isVip = this.$store.state.globalData.userInfo.is_member
		this.getList()
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			console.log(res.target)
		}
		return {
			title: '龙腾虎跃商城',
			path: '/pages/tabbar/tabbar-1/tabbar-1',
			imageUrl: '/static/img/logo.png'
		}
	},
	methods: {
		//接收子组件传递的数量
		setGoodsNum(data) {
			this.changeNum(data)
		},

		//是否编辑
		editIng() {
			this.isEdit = !this.isEdit
		},

		//去结算
		goPay() {
			if (this.cargoNum == 0) {
				return uni.showToast({
					title: '请先选中商品哦~',
					icon: "none"
				})
			}
			if (this.currentTab == 0) {
				uni.navigateTo({
					url: '/pages/pay/pay'
				})
			} else {
				uni.navigateTo({
					url: '/pages/pay/otherpay/otherpay?field=' + this.currentTab
				})
			}

		},

		tabChange: function(index) {

			this.currentTab = index
			this.getList()
		},

		//获取购物车列表
		getList() {
			var that = this
			// this.nodata = false
			this.$api.get('/api/cart/getList', {
				field: this.currentTab
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.listInfo = res.data.data
					var list = res.data.data.list
					var normalList = []
					var outList = []
					if (list.length == 0) {
						that.list = list
						that.nodata = true
					} else {
						that.nodata = false
						for (var i = 0; i < list.length; i++) {
							if (list[i].is_valid == 1) {
								normalList.push(list[i])
							} else {
								outList.push(list[i])
							}
						}
						console.log(normalList, outList)
						that.list = normalList
						that.overdueList = outList
						that.isCheckedAll()
					}

				}
			})
		},

		goDetail(item) {
			uni.navigateTo({
				url: '/pages/goods_detail/goods_detail?id=' + item.field_id + '&field=' + this.currentTab
			})
		},

		//全选或是反选
		selAll() {
			var that = this
			var list = this.list
			that.selectAll = !that.selectAll
			var checkId = ''
			for (var i in list) {
				list[i].checked = that.selectAll
				checkId += list[i].id + ','
			}
			that.list = list
			checkId = checkId.slice(0, checkId.length - 1)
			that.changeCheck(null, checkId)
		},

		//更新数量
		changeNum(data) {
			var that = this
			this.$api.get('/api/cart/changeNum', {
				cart_id: data.id,
				num: data.num
			}, function(res) {
				if (res.data.code != 200) {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				} else {
					that.getList()
				}
			})
		},
		//更新选中状态
		changeCheck(index, id) {
			var that = this
			if (index != null) {
				var list = this.list
				list[index].checked = list[index].checked == 0 ? 1 : 0
				that.list = list
				that.isCheckedAll()
			}
			this.$api.get('/api/cart/changeChecked', {
				field: this.currentTab,
				cart_id: id
			}, function(res) {
				if (res.data.code == 200) {
					that.getList()
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},

		//验证是否全选
		isCheckedAll() {
			var that = this
			var checkedNum = 0
			var cargoNum = 0
			for (var i in that.list) {
				if (that.list[i].checked == 1) {
					checkedNum++
					cargoNum += that.list[i].number
				}
			}
			that.cargoNum = cargoNum
			if (checkedNum == that.list.length) {
				that.selectAll = true
			} else {
				that.selectAll = false
			}
		},

		//删除选中
		deleteCargo() {
			var that = this
			uni.showModal({
				title: '确认删除选中商品吗?',
				confirmColor: '#e9101f',
				success: function(res) {
					if (res.confirm) {
						var list = that.list
						var checkId = ''
						for (var i in list) {
							if (list[i].checked == 1) {
								checkId += list[i].id + ','
							}
						}
						checkId = checkId.slice(0, checkId.length - 1)
						that.$api.get('/api/cart/delCart', {
							cart_id: checkId
						}, function(res) {
							if (res.data.code == 200) {
								that.getList()
								that.isEdit = false
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								})
							}
						})
					}
				}
			})
		},

		//删除失效
		delOverdue(item, idx) {
			var that = this
			uni.showModal({
				title: '确认删除选中商品吗?',
				confirmColor: '#e9101f',
				success: function(res) {
					if (res.confirm) {
						that.$api.get('/api/cart/delCart', {
							cart_id: item.id
						}, function(res) {
							if (res.data.code == 200) {
								that.getList()
								that.isEdit = false
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								})
							}
						})
					}
				}
			})
		},
		//获取开放区域
		// 			getZone() {
		// 				var that = this
		// 				this.$api.get('/api/index/getInfo', {}, function(res) {
		// 					console.log(res)
		// 					if (res.data.code == 200) {
		// 						that.zoneInfo = res.data.data
		// 					}
		// 				})
		// 			},

		// 			closePop() {
		// 				this.showPop = false,
		// 					this.showCloseBut = false
		// 			}
	}
}
